import { assembleLocString, doesIntersect2, getContainingTrack, getContainingView, getSession, isSessionModelWithWidgets, } from '@jbrowse/core/util';
import { MAX_COLOR_RANGE, getId } from '../drawSynteny';
export function drawMatchSimple({ feature, ctx, offsets, level, cb, height, drawCurves, oobLimit, viewWidth, hideTiny, }) {
    const { p11, p12, p21, p22 } = feature;
    const x11 = p11.offsetPx - offsets[level];
    const x12 = p12.offsetPx - offsets[level];
    const x21 = p21.offsetPx - offsets[level + 1];
    const x22 = p22.offsetPx - offsets[level + 1];
    const l1 = Math.abs(x12 - x11);
    const l2 = Math.abs(x22 - x21);
    const y1 = 0;
    const y2 = height;
    const mid = (y2 - y1) / 2;
    const minX = Math.min(x21, x22);
    const maxX = Math.max(x21, x22);
    if (!doesIntersect2(minX, maxX, -oobLimit, viewWidth + oobLimit)) {
        return;
    }
    if (l1 <= 1 && l2 <= 1) {
        if (!hideTiny) {
            ctx.beginPath();
            ctx.moveTo(x11, y1);
            if (drawCurves) {
                ctx.bezierCurveTo(x11, mid, x21, mid, x21, y2);
            }
            else {
                ctx.lineTo(x21, y2);
            }
            ctx.stroke();
        }
    }
    else {
        draw(ctx, x11, x12, y1, x22, x21, y2, mid, drawCurves);
        cb(ctx);
    }
}
export function draw(ctx, x1, x2, y1, x3, x4, y2, mid, drawCurves) {
    if (drawCurves) {
        drawBezierBox(ctx, x1, x2, y1, x3, x4, y2, mid);
    }
    else {
        drawBox(ctx, x1, x2, y1, x3, x4, y2);
    }
}
export function drawBox(ctx, x1, x2, y1, x3, x4, y2) {
    ctx.beginPath();
    ctx.moveTo(x1, y1);
    ctx.lineTo(x2, y1);
    ctx.lineTo(x3, y2);
    ctx.lineTo(x4, y2);
    ctx.closePath();
}
export function drawBezierBox(ctx, x1, x2, y1, x3, x4, y2, mid) {
    const len1 = Math.abs(x1 - x2);
    const len2 = Math.abs(x1 - x2);
    if (len1 < 5 && len2 < 5 && x2 < x1 && Math.abs(x1 - x3) > 100) {
        const tmp = x1;
        x1 = x2;
        x2 = tmp;
    }
    ctx.beginPath();
    ctx.moveTo(x1, y1);
    ctx.lineTo(x2, y1);
    ctx.bezierCurveTo(x2, mid, x3, mid, x3, y2);
    ctx.lineTo(x4, y2);
    ctx.bezierCurveTo(x4, mid, x1, mid, x1, y1);
    ctx.closePath();
}
export function onSynClick(event, model) {
    const view = getContainingView(model);
    const track = getContainingTrack(model);
    const { featPositions, numFeats, clickMapCanvas: ref1, cigarClickMapCanvas: ref2, level, } = model;
    if (!ref1 || !ref2) {
        return;
    }
    const rect = ref1.getBoundingClientRect();
    const ctx1 = ref1.getContext('2d');
    const ctx2 = ref2.getContext('2d');
    if (!ctx1 || !ctx2) {
        return;
    }
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;
    const [r1, g1, b1] = ctx1.getImageData(x, y, 1, 1).data;
    const unitMultiplier = Math.floor(MAX_COLOR_RANGE / numFeats);
    const id = getId(r1, g1, b1, unitMultiplier);
    const feat = featPositions[id];
    if (feat) {
        const { f } = feat;
        model.setClickId(f.id());
        const session = getSession(model);
        if (isSessionModelWithWidgets(session)) {
            session.showWidget(session.addWidget('SyntenyFeatureWidget', 'syntenyFeature', {
                view,
                track,
                featureData: f.toJSON(),
                level,
            }));
        }
    }
    return feat;
}
export function onSynContextClick(event, model, setAnchorEl) {
    event.preventDefault();
    const ref1 = model.clickMapCanvas;
    const ref2 = model.cigarClickMapCanvas;
    if (!ref1 || !ref2) {
        return;
    }
    const rect = ref1.getBoundingClientRect();
    const ctx1 = ref1.getContext('2d');
    const ctx2 = ref2.getContext('2d');
    if (!ctx1 || !ctx2) {
        return;
    }
    const { clientX, clientY } = event;
    const x = clientX - rect.left;
    const y = clientY - rect.top;
    const [r1, g1, b1] = ctx1.getImageData(x, y, 1, 1).data;
    const unitMultiplier = Math.floor(MAX_COLOR_RANGE / model.numFeats);
    const id = getId(r1, g1, b1, unitMultiplier);
    const f = model.featPositions[id];
    if (f) {
        model.setClickId(f.f.id());
        setAnchorEl({ clientX, clientY, feature: f });
    }
}
export function getTooltip({ feature, cigarOp, cigarOpLen, }) {
    const f1 = feature.toJSON();
    const f2 = f1.mate;
    const l1 = f1.end - f1.start;
    const l2 = f2.end - f2.start;
    const identity = f1.identity;
    const n1 = f1.name;
    const n2 = f2.name;
    return [
        `Loc1: ${assembleLocString(f1)}`,
        `Loc2: ${assembleLocString(f2)}`,
        `Inverted: ${f1.strand === -1}`,
        `Query len: ${l1.toLocaleString('en-US')}`,
        `Target len: ${l2.toLocaleString('en-US')}`,
        identity ? `Identity: ${identity.toPrecision(2)}` : '',
        cigarOp ? `CIGAR operator: ${cigarOp}${cigarOpLen}` : '',
        n1 ? `Name 1: ${n1}` : '',
        n2 ? `Name 2: ${n2}` : '',
    ]
        .filter(f => !!f)
        .join('<br/>');
}
